.header {
  .toggle-bar {
  cursor: pointer;
  }
  .nav {
  padding: 10px;
  display: flex;
  align-items: center;
  color: #FFF;
  img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  }
  .list {
  list-style: none;
  li {
  display: inline-block;
  padding: 10px;
  text-transform: uppercase;
  }
  }
  .input {
  border: 1px solid;
  padding: 8px;
  input {
  padding: 6px;
  color: #000;
  outline: none;
    background: transparent;
    color: #FFF;
    &::placeholder {
        color: #FFF;
        transition: opacity .5s;
      }
      &:focus::placeholder {
        opacity: 0;
      }
}
button {
      background: red;
      padding: 6px 10px
}
  }
  }
svg {
  cursor: pointer;
}
}