
.customers {
  position: relative;
  overflow: hidden;
.container {
  .comment-box {
    position: relative;
    height: 300px;
    width: 100%;
  }
  .customer {
    position: absolute;
    align-items: center;
    padding: 10px;
    padding: 25px;
    width: 340px;
    background-color: #FFF;
    opacity: 0;

    transform: translate(-50%, -50%);
    top: 50%;
    left:50%;
    transition: all 1s;
    &.previous {
      opacity: .5s;
      transform: translate(-150%, -50%) scale(.8);
    }
    &.current {
    opacity: 1;
    .rate {
      svg {
        transition:  all .3s ease-in-out;
        color: gold;
      }
    }
    }
    &.next {
     opacity: .5s;
     transform: translate(50%, -50%) scale(.8);
    }
    .personal {
     position: absolute;
     top: -26px;
     left: 42%;
     border-radius: 50%;
     border: 2px solid #fff;
  
     img {
      width: 50px;
      height: 50px;
      border-radius: 50%;
     }
  
     h4 {
      margin-top: 10px;
     }
    }
    .comment {
     p {
      margin-top: 8px;
     }
    }
  }
  .leave-comment {
    button {
      padding: 6px 10px;
      background-color: red;
      color: #FFF;
      svg {
        margin-left: 5px;
      }
    }
  }
  .add-comment {
    position: fixed;
    top: 50%;
    left: 50%;
    width: 400px;
    height: 400px;
  }
  .add-comment-box {
    left: 50%;
    display: none;
    overflow: hidden;
    position: absolute;
    top: 50%;
    width: 390px;
    height: 372px;
    transform: translate(-50%, -50%);
    text-align: center;
    padding: 10px;
    border-radius: 6px;
    .cross-icon {
      position: absolute;
      right: 0;
      width: 60px;
      height: 60px;
      background: #FFF;
      top: -14px;
      display: flex;
      align-items: center;
      border-radius: 50%;
      right: -17px;
      padding: 10px;
      svg {
        padding: 8px;
        margin-top: 7px;
        cursor: pointer;
      }
    }
    p {
      color: #FFF;
      font-weight: bold;
      font-size: 22px;
    }
    .avatar {
      border: 1px solid #FFF;
    }
    .input {
      border-radius: 6px;
      display: block;
      width: 250px;
      margin: 10px auto;
      padding: 5px 10px;
      outline: none;
      transition: all .5s;
      &:focus::placeholder {
        opacity: 0;
      }
    }
    textarea {
        resize: none;
        height: 150px;
    }
    .img {
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 5px 0;
      img {
        cursor: pointer;
        margin-left: 8px;
        width: 30px;
        height: 30px;
        border-radius: 50%;
      }
    }
    .choose {
      font-size: 15px;
      color: #FFF;
      font-weight: bold;
    }
    .send {
      background-color: white;
      padding: 4px 10px;
      width: 91px;
      margin: 0 auto;
      margin-top: 10px; 
      border-radius: 4px;
      font-weight: bold;
    }
  }
}
}