.landing-page {
 width: 100%;
 height: calc(100vh - 74px);
 z-index: -1;
 overflow: hidden;
 position: relative;
 .content-img {
  height: 100%;
  width: 100%;
  .box{
   position: absolute;
   width: 100%;
   height: 100%;
   opacity: 0;
   transition: opacity 2s ease-in-out, transform 2s ease-in-out;
  //  transform: translateX(-100%);
   &.active {
    opacity: 1;
   }
   img {
     width: 100%;
     max-width: 100%;
     height: 100vh;
     object-fit: cover;
     background-position: center;
   }
   p {
    position: absolute;
    top: 0;
    left: 0;
    background-color: #e55a5a6b;
    color: #FFF;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 22px;
    padding: 20px;
    text-align: center;
   }
  }
 }
}