.sidebar {
 width: 250px;
 background: #FFF;
 height: 100vh;
 position: fixed;
 top: 0;
 left: -250px;
 z-index: 100;
 .icon{
 width: 30px;
 height: 30px;
 position: absolute;
 right: 30px;
 top: 12px;
 background: #ffffff30;
 align-items: center;
 justify-content: center;
 svg {
  color: #FFF;
  cursor: pointer;
 }
 }
 transition: all .5s;
 background: #f87171;
 &.toggle-sidebar {
  left: 0;
 }
 .link {
  a{
   display: block;
   padding: 10px 15px;
   color: #FFF;
   position: relative;
   text-transform: uppercase;
   transition: all .5s;
   &::before {
    content: '';
    position: absolute;
    width: 0%;
    height: 100%;
    top: 0;
    left: 0;
    background: #ffffff21;
    transition: all .5s;
    z-index: -1;
   }
   &:hover::before {
    width: 100%;
   }
   &:hover {
    padding-left: 25px;
   }
  }
 }
}