.options-box {
 position: fixed;
 right: -250px;
 top: 0;
 z-index: 1000;
 width: 250px;
 height: 100vh;
 transition: all .5s; 
 &.hide {
  right: 0;
 }
 .option-box {
  margin: 10px 0;
  border: 1px solid #77777745;
  padding: 20px;
 }
 background-color: #FFF;
 padding: 15px;
 text-align: center;
 .gear-box {
  position: absolute;
  left: -36px;
  cursor: pointer;
  top: 100px;
  background-color: #FFF;
  padding: 10px;
  border-radius: 4px 0 4px 4px;
 }
  ul {
   display: flex;
   align-items: center;
   justify-content: center;
   li {
    width: 20px;
    height: 20px;
    background: var(--i);
    border-radius: 50%;
    margin-left: 5px;
    cursor: pointer;
    transition: all .5s;
    opacity : .6;
    &.active {
      opacity: 1;
    }
  }
  }
}