.order {
 position: absolute;
 width: 80%;
 display: none;
 min-height: 500px;
 background-color: red;
 top: 95px;
 left: 11%;
 border-radius: 6px;
 color: #FFF;
 padding: 15px;
 svg {
   position: absolute;
   right: 10px;
   top: 6px;
   cursor: pointer;
   padding: 5px;
 }
 .order-list {
  margin-top: 22px;
  transition: all .5s;
  li {
   background-color: violet;
   display: flex;
   align-items: center;
   justify-content: space-between;
   margin: 5px 0;
   padding: 10px;
   transition: all .5s;
  box-shadow: 1px 0px 3px 1px;
  }
 }
}