.seller {
 @mixin button {
   display: block;
   background-color: red;
   display: block;
   padding: 5px 10px;
   border-radius: 4px;
   color: #FFF;
   font-weight: bold;
  }
 .sliders {
  .slide {
   .slide-content {
    display: flex;
    // flex-wrap: wrap;
    align-items: center;
    width:95%;
    border-radius: 6px;
    background-color: #FFF;
    padding: 10px;
    transition: all .5s;
    // overflow: hidden;
    &:hover {
     transform: translateY(-5px);
    }
    .img {
     margin-right: 5px;
     width: 180px;
     height: 100%;
     margin-right: 10px;
     img {
      object-fit: cover;
      width: 100%;
      height: 142px;
     }
    }
    .text {
     margin-left: 10px;
     overflow: hidden;
     width: 50%;
     h4 {
      font-weight: bold;
      margin-bottom: 8px;
     }
     p {
      color: #444;
      width: 180px;
      margin: 8px 0;
     }
     span {
         font-size: 14px;
         font-weight: bold;
     }
     button {
      @include button
     }
    }
   }
  }
 }
 .more {
  margin: 2rem auto;
  text-align: center;
  a {
   @include button;
   margin: 10px auto;
   width: fit-content;
   padding: 5px 10px;
   svg {
    margin-left: 5px;
     margin-top: 2px;
     transition: all .5s;
   }
   &:hover {
    svg {
     margin-left: 8px;
    }
   }
  }
 }
}
