.shop {
 .items {
  .gallery {
   .gallery-box {
    background-color: #FFF;
    overflow: hidden;
    position: relative;
    img {
     transition: .5s;
    }
    &:hover {
        img:hover {
      transform : scale(1.3) rotate(10deg);
     }
    }
   }
  }
  a {
   background-color: red;
   padding: 8px 15px;
   color: #FFF;
   font-weight: bold;
  }
 }
}