 * {
  margin:0;
  margin : 0;
  box-sizing: border-box;
}

::-webkit-scrollbar {
  background-color: #FFF;
}

  ::-webkit-scrollbar-thumb {
  background-color: #8080807a;
}

 body {
  position: relative;
  transition: all .5s;
  font-family: 'Open Sans', sans-serif;
  &.sidebar-padding {
   padding-left: 240px;
   transition: padding-left .5s;
   overflow: hidden;
  }
  @media(max-width: 767px) {
    &.sidebar-padding {
      padding-left: 25px;
    }
  }
  .head-line {
    text-align: center;
    margin-bottom: .5rem;
    h3 {
      font-weight: bold;
      font-size: 1.5rem;
      position: relative;
      width :fit-content;
      margin: 0 auto;
      color: #FFF;
      &::before {
        content: '';
          position: absolute;
          bottom: -7px;
          background: #f87171;
          width: 100%;
          left: 0;
          height: 5px;
          transform: skewX(53deg);
      }
    }
    p {
      color: #FFF;
      font-size: 20px;
    }
  }

  &.dancing-script {
    font-family: 'Dancing Script', cursive;
  }

  &.inter {
    font-family: 'Inter', sans-serif;
  }

  &.open-sans {
    font-family: 'Open Sans', sans-serif;
  }

  &.poppins {
    font-family: 'Poppins', sans-serif;
  }

  &.robot {
    font-family: 'Roboto', sans-serif;
  }

  &.roboto-condensed {
    font-family: 'Roboto Condensed', sans-serif;
  }

  &.tajawal {
    font-family: 'Tajawal', sans-serif;
  }
}



