$themes-gradient: (
 from-red-700: (color: #c62d2d),
 from-cyan-700: (color: rgb(15 109 136)),
 from-rose-700: (color: rgb(199 32 71)),
 from-stone-700: (color: rgb(85 81 77)),
);


@each $theme,
$map in $themes-gradient {
 .#{$theme} {
  .sidebar {
   background-color: map-get($map, color) !important;
  }
    .order {
     background-color: map-get($map, color) !important;
    }
   .order .order-list li {
    background-color: map-get($map, color) !important;
   }
    .customers .container .add-comment-box {
    background-color: map-get($map, color) !important;
   }
   .customers .container .add-comment-box .send {
    color: map-get($map, color) !important;
   }
   .search {
    background-color: map-get($map, color) !important;
   }
   .search-box .btn {
    color: map-get($map, color) !important;
   }
 }
}
$themes-natural: (
 bg-orange-600: (color: #ea580c),
 bg-indigo-500: (color: #6366f1),
 bg-stone-600: (color: #57534e),
);


@each $theme,
$map in $themes-natural {
